<template>
  <div class="app-container">
    <div id="cim"></div>
  </div>
</template>

<script>
import engine from "../engine";
let rtc;
export default {
  name: "home",
  data() {
    return {
      rtc:null,
    };
  },
  mounted() {
    
    // engine.run("wss://engine.xscitydo.com/sws/player/56wAKqi3yBALX7KZBXzqxjcphlv7RMfg","cim").then(result=>{
    engine.run("ws://"+location.hostname+(location.port ? ":"+location.port : "")+"/player","cim").then(result=>{
        this.rtc=result.rtc;
        result.msg.responseHandel=(response)=>{
            console.log("收到response消息",response);
        }
        // rtc.send(new engine.action.test.test()).then(()=>{
        //     console.log("存档加载成功")
        // });
    }).catch(msg=>{
        console.error(msg);
    });
  },
  methods: {
    getQueryString:(name)=>{ 
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i"); 
        var r = window.location.search.substr(1).match(reg); 
        if (r != null) return unescape(r[2]); 
        return null; 
    } 
  }
};
</script>


<style>
body{
  padding: 0px;
  margin:0px;
}
#cim{
  position:absolute;
  width:100%;
  height: 100%;
}
</style>


